<template>
  <div class="user">
    <headpage></headpage>
    <center>
    <!-- 用户信息 -->
    <div class="userInfo">
    <span>{{username}}</span>
    </div>
    <!-- 相关操作 -->
    <div class="backToIndex">
    <router-link tag="span" to="index">
      <span>返回首页</span>
    </router-link>
    </div>
    <div>
    <button class="btn__logout" type="button" @click="logout">注销登录</button>
    </div>
    </center>
    <footpage></footpage>
  </div>
</template>

<script>
import headpage from "@/components/headbar";
import footpage from "@/components/footbar"
export default {
  data() {
    return {
      username: sessionStorage.getItem("userName")
    };
  },
  components: {
    headpage,
    footpage
  },
  methods: {
    //注销登录
    logout() {
      sessionStorage.setItem("userName", null);
      this.$store.dispatch("userName", null);
      alert("注销成功");
      this.$router.push({ path: "./" });
    },
    beforeRouteEnter: (to, from, next) => {
      next(vm => {
        vm.$store.dispatch("setUser", null);
      });
    }
  }
};
</script>

<style lang="less" scoped>
.user{
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.userInfo{
    margin-top: 40px;
    font-size: 20px;
    color: 	#191970;
}
.backToIndex{
    margin-top: 20%;
    font-size: 20px;
    color: 	#191970;
}
.btn__logout{
    border: none;
    background: none;
    margin-top: 5px;
    font-size: 20px;
    color: 	#191970;
}
</style>



