<template>
  <div id="login">
    <div class="login__head">
      <img class="logo" src="../img/logo.png" alt="logo" />
      <span class="name">Dermatopathology Analysis 皮肤病理 AI 分析 </span>
    </div>
     <div class="login__form"> 
        <li>
          <span>用户名：</span>
          <input type="text" v-model="loginForm.username" placeholder="请输入用户名" />
        </li>
        <br />
        <li>
          <span>密&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
          <input type="password" v-model="loginForm.password" placeholder="请输入密码" />
        </li>
        <br />
        <li>
          <button type="submit" class="btn btn-primary" @click.prevent="login">LOGIN</button>
        </li>
    </div>
    <footpage></footpage>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import axios from "axios";
import qs from "qs";

import footpage from "@/components/footbar"

export default {
  name: "login",
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  components: {
    footpage
  },
  methods: {
    login() {
      let _this = this;
      let params = {
        username: this.loginForm.username,
        password: this.loginForm.password,
        dt:Date().toLocaleString()
      };
      if (this.loginForm.username == "" || this.loginForm.password == "") {
        alert("账号或密码不能为空");
       }
       else if(this.loginForm.username == "user" && this.loginForm.password == "12345678"){
            alert("登录成功！");
            sessionStorage.setItem("userName", this.loginForm.username);
            this.$store.dispatch("userName", this.loginForm.password);
            this.$router.push({ path: "./index" });
       }
       else{
         alert("用户名或密码错误，请重新输入")
       }
      // else {
      //   axios.post("http://152.136.211.215:80/login", qs.stringify(params)).then(response => {
      //     console.log(response);
      //     if (response.status === 200) {
      //       alert("登录成功！");
      //       sessionStorage.setItem("userName", response.data.username);
      //       this.$store.dispatch("userName", response.data.username);
      //       // this.$router.push({ path: "./index" });
      //       return true;
      //     } else {
      //       alert("账号或密码错误，请重新输入");
      //       return false;
      //     }
      //   });
      // }
    }
  }
};
</script>

<style lang="less" scoped>
#login {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.logo {
  width: 19.2%;
  height: 5%;
  margin-top: 1%;
  margin-left: 5%;
}
.name {
  font-size: 2.5rem;
  margin-left: 1%;
  color: #191970;
}
li {
  list-style: none;
}
.login__form {
  width: 30%;
  height: 30%;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  span{
    font-size: 20px;
  }
  input{
    width: 40%;
    height: 20%;
  }
}
button {
  width: 40%;
}
</style>



