<template>
  <div class="index">
    <headpage></headpage>
    <center>
    <div class="msg">
      <button id="upload" @click="getFile">
      点击 
      <span class="glyphicon glyphicon-new-window"></span>
      上传皮肤病理图片
      </button>
    <input type="file" accept=".jpg,.png,.gif,.bmp,.jpeg" style="display:none" id="getF" @change="clickF" />
    </div>
    </center>
    <footpage></footpage>
  </div>
</template>

<script>
import headpage from "@/components/headbar";
import footpage from "@/components/footbar"
export default {
  data() {
    return{
    }
  },
  components: {
    headpage,
    footpage
  },
  methods: {
  //选择文件
  getFile() {
    $("#getF").click();
  },
  clickF(event) {
    //获取文件名和文件信息，传给后台数据库
    
    console.log("hhh")
    let file = event.target.files[0];
    let $filePath=URL.createObjectURL(event.target.files[0]);
    console.log("我是index")
    let filename = $("#getF").val();
    let filenames1 = filename.split("\\");
    let filename2 = filenames1[filenames1.length - 1];
    let filenames2 = filename2.split(".");
    filename = filenames2[0];
    let param = new FormData(); //创建form对象
    param.append('qqfile',file);//通过append向form对象添加数据
    param.append('fileName',filename)
    let config = {
      headers:{'Content-Type':'multipart/form-data'}
    };  //添加请求头
      axios
        .post("http://123.206.92.104:8009/upload",param,config)
        .then(response =>{
          if(response.data.success === true){
            alert("上传成功");
            this.$router.push({ 
              path: "./prediction",
              //将图片路径以及图片名传给预测页面
              query:{
                fileUrl:$filePath,
                processed_file: filename
              }
              });
          }else{
            alert("上传失败")
          }
        })
  }
  },
  
};
</script>

<style lang="less" scoped>
.index{
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
#upload {
  border: none;
  background: none;
}
.msg{
  margin-top: 15%;
  font-size: 3rem;
  color: #191970;
  span{
    font-size: 4rem
  }
}

</style>


