<template>
  <div>
    <img class="logo" src="../img/logo.png" alt="logo" />
    <div class="name">Dermatopathology Analysis 皮肤病理 AI 分析</div>
    <!-- 刷新 -->
    <router-link tag="span" to="index">
      <span id="refresh" class="glyphicon glyphicon-repeat"></span>
    </router-link>
    <!-- 个人中心 -->
    <router-link tag="span" to="user">
      <span id="user" class="glyphicon glyphicon-user"></span>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";
import { mapMutations } from "vuex";
import qs from "qs";

export default {
  data() {
    return {
      username: sessionStorage.getItem("userName"),
      $filePath: "",
      filename: ""
    };
  },
  methods: {
    // 判断是否登录,如果没有登录，则提示登录，并跳回登录页面
    isLogin() {
      if (sessionStorage.getItem("userName") !== "null") {
        this.$store.commit("userStatus", sessionStorage.getItem("userName"));
      } else {
        this.$store.commit("userStatus", null);
        alert("请先登录！");
        this.$router.push({ path: "./" });
      }
      return this.$store.getters.isLogin;
    },
  },
  mounted() {
    this.isLogin();
  }
};
</script>

<style lang="less" scoped>

// logo
.logo {
  width: 19.2%;
  height: 5%;
  margin-top: 1%;
  margin-left: 5%;
}
// 标题
.name {
  display: inline-table;
  margin-left: 1%;
  width: 40%;
  height: 3%;
  color: #191970;
  font-size: 2.5rem;
}
span {
  font-size: 2.5rem;
}
// 刷新、用户中心按钮
#refresh{
  color: #191970;
  margin-top: 1%;
  margin-left: 20%;
}
#user {
  color: #191970;
  margin-top: 1%;
  margin-left: 5%;
}
</style>



