<template>
  <div>
    <img class="logo" src="../img/logo.png" alt="logo" />
    <div class="name">{{name}}</div>
    <div class="ing" id="ing">
      <img src="../img/i.gif" />
      <span style="text-algin:center">上传中...</span>
    </div>
    <!-- 上传图片 -->
    <button id="upload" @click="getFile">
      <span class="glyphicon glyphicon-new-window"></span>
    </button>
    <!-- 规定上传图片的格式 -->
    <input
      type="file"
      accept=".jpg, .png, .gif, .bmp, .jpeg"
      style="display:none"
      id="getF"
      @change="clickF"
    />
    <!-- 刷新 -->
    <router-link tag="span" to="index">
      <span id="refresh" class="glyphicon glyphicon-repeat"></span>
    </router-link>
    <!-- 个人中心 -->
    <router-link tag="span" to="user">
      <span id="user" class="glyphicon glyphicon-user"></span>
    </router-link>
    <!-- 上传成功提示框 -->
    <div id="success-window" class="success">
      <br />
      <span class="success__head">贝叶科技皮肤病理 AI 分析系统提示:</span>
      <br />
      <br />
      <span class="success__body">上传成功</span>
      <br />
      <br />
      <button id="ok" @click="ok" class="btn btn-primary">确认</button>
    </div>
    <div class="shadow" id="shadow"></div>
  </div>
</template>

<script>
import axios from "axios";
import { mapMutations } from "vuex";
import qs from "qs";

export default {
  data() {
    return {
      name:"Dermatopathology Analysis 皮肤病理 AI 分析",
      username: sessionStorage.getItem("userName"),
      $filePath: "",
      filename: ""
    };
  },
  methods: {
    // 判断是否登录,如果没有登录，则提示登录，并跳回登录页面
    isLogin() {
      if (sessionStorage.getItem("userName") !== "null") {
        this.$store.commit("userStatus", sessionStorage.getItem("userName"));
      } else {
        this.$store.commit("userStatus", null);
        alert("请先登录！");
        this.$router.push({ path: "./" });
      }
      return this.$store.getters.isLogin;
    },
    // 获取当前年月日，格式为190726
    getDate() {
      let d = new Date();
      let year = d.getFullYear();
      year = year.toString().slice(2, 4);
      let month = d.getMonth() + 1;
      let day = d.getDate();
      if (month >= 1 && month <= 9) {
        month = "0" + month;
      }
      if (day >= 0 && day <= 9) {
        day = "0" + day;
      }
      let dt = year + month.toString() + day.toString();
      return dt;
    },
    //获取当前小时分钟秒数,格式为171355
    getTime() {
      let d = new Date();
      let hour = d.getHours();
      let minute = d.getMinutes();
      let second = d.getSeconds();
      if(hour >= 1 && hour <= 9) {
        hour = "0" + hour;
      }
      if(minute >= 1 && minute <= 9) {
        minute = "0" + minute;
      }
      if(second >= 1 && second <= 9) {
        second = "0" + second;
      }
      let dt = hour.toString()+minute.toString()+second.toString();
      return dt;
    },
    //选择文件
    getFile() {
      $("#getF").click();
    },
    clickF(event) {
      $("#ing").toggle();
      //将上传的图片显示在下一页面，解析后的绝对路径
      this.$filePath = URL.createObjectURL(event.target.files[0]);
      //获取文件名和文件信息，传给后台数据库
      let file = event.target.files[0];

      // this.filename = $("#getF").val();
      // let filenames1 = this.filename.split("\\");
      // let filename2 = filenames1[filenames1.length - 1];
      // let filenames2 = filename2.split(".");
      // this.filename = filenames2[0];

      this.filename= "A00"+"-"+this.$options.methods.getDate()+"-"+this.$options.methods.getTime();
      console.log(this.filename);
      let param = new FormData(); //创建form对象
      param.append("qqfile", file); //通过append向form对象添加数据
      param.append("fileName", this.filename);
      let config = {
        headers: { "Content-Type": "multipart/form-data" }
      }; //添加请求头
      axios
        .post("http://123.206.92.104:8009/upload", param, config)
        .then(response => {
          if (response.data.success === true) {
            $("#ing").toggle();
            $("#success-window").toggle();
            $("#shadow").toggle();
          } else {
            alert("上传失败");
          }
        });
    },
    ok() {
      $("#success-window").toggle();
      $("#shadow").toggle();
      this.$router.push({
        path: "./prediction",
        //将图片路径以及图片名传给预测页面
        query: {
          fileUrl: this.$filePath,
          processed_file: this.filename
        }
      });
    }
  },
  mounted() {
    this.isLogin();
  }
};
</script>

<style lang="less" scoped>
.ing {
  display: none;
  position: fixed;
  top: 10%;
  left: 40%;
  z-index: 3;
}
// logo
.logo {
  width: 19.2%;
  height: 5%;
  margin-top: 1%;
  margin-left: 5%;
}
// 标题
.name {
  display: inline-table;
  margin-left: 1%;
  margin-top: 2%;
  width: 40%;
  height: 3%;
  color: #191970;
  font-size: 2.5rem;
}
span {
  font-size: 2.5rem;
  margin-top: 2%;
}
// 上传按钮
#upload {
  margin-top: 1%;
  margin-left: 8%;
  border: none;
  background: none;
}
#upload span {
  color: #191970;
}
// 刷新、用户中心按钮
#refresh,
#user {
  color: #191970;
  margin-top: 1%;
  margin-left: 5%;
}
.success {
  display: none;
  position: fixed;
  z-index: 5;
  top: 0;
  left: 33%;
  width: 40rem;
  height: 18rem;
  border: 1px solid rgb(245, 245, 245);
  border-radius: 4px;
  background-color: rgb(245, 245, 245);
  &__head {
    font-size: 2rem;
    color: #191970;
    margin: 0 0 0 5%;
  }
  &__body {
    font-size: 3rem;
    color: #191970;
    margin: 0 0 0 35%;
  }
  #ok {
    margin: 0 0 0 80%;
  }
}
/*遮罩层*/
#shadow {
  width: 100%;
  height: 100%;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 4;
  opacity: 0.3;
  filter: alpha(opacity=30);
  display: none;
}
</style>



