<template>
  <div class="prediction">
    <headpage></headpage>
    <div class="body">
      <!-- 左侧内容 -->
      <div class="left">
        <span class="left__msg">上传的病理图片</span>
        <br />
        <img class="left__img" id="illness" :src="this.url" alt="病理图片" />
      </div>
      <!-- 中间内容 -->
      <div class="center">
        <div class="center__process" id="process">
        <img src="../img/iii.gif">
        <br>
        <br>
        <span>分析中...</span>
        </div>
        <!-- <span class="center-prediction" id="prediction">疑似{{msg}}</span> -->
        <div class="center__prediction" id="prediction">
            <span>炎症模式</span>
            <div class="center__prediction-msg">
              <span >&nbsp;&nbsp;{{msg}}</span>
              <br />
              <span class="center__prediction-info">&nbsp;&nbsp;浅层血管周围皮炎</span>
            </div>
        </div>
      </div>
      <!-- 右侧内容 -->
      <div class="right">
      <span>近似的病理图片</span>
      <div style="margin: 3% 0 0 0;">
        <img :src="this.imgSS.imgSrc0"/>
        <img :src="this.imgSS.imgSrc1" />
      </div>
      <div style="margin-top:3px">
        <img :src="this.imgSS.imgSrc2"/>
        <img :src="this.imgSS.imgSrc3" />
      </div>
      </div>
    </div>
    <footpage></footpage>
  </div>
</template>

<script>
import axios from "axios";
import headpage from "@/components/headbarPrediction";
import footpage from "@/components/footbar"

export default {
  data() {
    return {
      url: "",
      msg: "",
      //预测图片列表
      imgSS: {
        imgSrc0:require("../img/ii.gif"),
         imgSrc1:require("../img/ii.gif"),
         imgSrc2:require("../img/ii.gif") ,
         imgSrc3:require("../img/ii.gif")
      },
      imgList:[
        {
         imgSrc0:require("../img/0/8357C嗜酸细胞增多4倍.jpg"),
         imgSrc1:require("../img/0/8446B湿疹4倍.jpg"),
         imgSrc2:require("../img/0/8480D海绵4倍.jpg") ,
         imgSrc3:require("../img/0/8512C海绵10倍.jpg")
        },
        {
         imgSrc0:require("../img/1/9494C.jpg"),
         imgSrc1:require("../img/1/9494D.jpg"),
         imgSrc2:require("../img/1/9495A.jpg"),
         imgSrc3:require("../img/1/9562E.jpg") 
        },
        {
         imgSrc0:require("../img/2/8983D.jpg"),
         imgSrc1:require("../img/2/9286B.jpg"),
         imgSrc2:require("../img/2/9373B.jpg"),
         imgSrc3:require("../img/2/9208C.jpg") 
        },
        {
         imgSrc0:require("../img/3/8956B硬萎4倍.jpg"),
         imgSrc1:require("../img/3/9016D扁平苔藓4倍.jpg"),
         imgSrc2:require("../img/3/9151B扁平苔藓4倍.jpg"),
         imgSrc3:require("../img/3/9151D扁平苔藓10倍.jpg") 
        },
        {
         imgSrc0:require("../img/4/10288A银屑病4倍.jpg"),
         imgSrc1:require("../img/4/10306A毛发红糠疹4倍.jpg"),
         imgSrc2:require("../img/4/8719B结节痒疹4倍.jpg"),
         imgSrc3:require("../img/4/10304A皮炎10倍.jpg") 
        },
      ]
    };
  },
  components: {
    headpage,
    footpage
  },
  created: function() {
    this.getParams();
  },
  watch: {
    // 监测路由变化,只要变化了就调用获取路由参数方法将数据存储本组件即可
    $route: "getParams"
  },
  methods: {
    getParams: function() {
      // 取到路由带过来的参数
      let fileUrl = this.$route.query.fileUrl;
      this.url = fileUrl
      let processed_file = this.$route.query.processed_file;
       axios
       .get("http://123.206.92.104:8009/predict?processed="+processed_file)
       .then(response => {
        let grade = response.data.grade;
        if(grade === 0){
          this.msg = "海绵"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[0]
        }else if(grade === 1){
          this.msg = "正常"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[1]
        }else if(grade === 2){
          this.msg = "单纯"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[2]
        }else if(grade === 3){
          this.msg = "苔癣"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[3]
        }else if(grade === 4){
          this.msg = "银屑"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[4]
        }else if(grade === 5){
          this.msg = "未分析"
          let imgS = eval(this.imgList)
          this.imgSS = imgS[5]
        }
        $("#process").toggle();
        $("#prediction").toggle();
      });
    }
  }
};
</script>


<style lang="less" scoped>
.prediction {
  background-image: url("../img/back.png");
  position: absolute;
  height: 100%;
  width: 100%;
}
.body {
  display: flex;
  flex-direction: row;
}
.left {
  margin: 5% 0 0 5%;
  height: 80%;
  width: 40%;
  &__msg {
    font-size: 2rem;
    color: #191970;
  }
  &__img {
    display: block;
    margin: 3% 0 0 0;
    height: 85%;
    width: 85%;
    border: 1px solid cornflowerblue
  }
}
.center{
  width: 20%;
  height: 10%;
  margin: 15% 0 0 4%;
  color: #191970;
  font-size: 2rem;
  &__process{
    text-align: center;
    img{
      width: 90%;
      height: 90%;
    }
  }
  &__prediction{
    display: none;
    &-msg{
      margin: 10% 0 0 0;
      background-color: orangered;
      color: white;
      font-size: 2.5rem;
    }
    &-info{
      font-size: 2rem;
    }
  }
}
.right{
  margin: 5% 0 0 10%;
  height: 80%;
  width: 40%;
  span{
    font-size: 2rem;
    color: #191970;
  }
  img{
    height: 42%;
    width: 42%;
    border: 0.5px solid cornflowerblue
  }
}
</style>




